	@include fonts("Inter", 500, 16px);
	z-index: 10;
	transition: 0.3s;
	position: fixed;
	width: 100%;
	min-height: 86px;
	padding: 0 150px;
	align-items: center;
	display: grid;
	grid-template-columns: 0.2fr 0.6fr 0.3fr;
	grid-template-rows: 1fr;
	gap: 0px;
	grid-template-areas: "logo nav-links icons";

