		background-position: center;
		background-size: contain;
		background-repeat: no-repeat;

		&:hover {
			transform: scale(1.05);
		}
	}

	ul {
		max-width: 507px;
		grid-area: nav-links;
		@include display(space-between);
		width: 100%;
		list-style-type: none;
		padding-right: 20px;

		li {
			padding-right: 5px;
			transition: 0.3s;

			&:hover {
				scale: 1.1;
			}
		}
	}

	.icons {
		max-width: 325px;
		grid-area: icons;
		@include display(space-between);

		.phone {
			white-space: nowrap;
			padding-right: 10px;
			align-self: center;
		}

		.phone:hover,
		.icons-logo div:hover a svg path {
			color: $blue-color;
			fill: $blue-color;
			transition: 0.2s;
		}

		.phone::selection {
			color: $black-color;
			background-color: $white-color;
		}

		&-logo {
			@include display(space-between);
			width: 100%;
			max-width: 110px;

			div {
				width: 25px;
				align-self: flex-end;
				z-index: 2;
				padding-right: 5px;
			}
		}
	}

	hr {
		position: absolute;
		bottom: 0;
		left: -5px;
	}
}